.Home {
  height: 100vh;
}

.Content {
  position: relative;
  top: 50%;
}

.Content h1 {
  font-size: calc(40px + (52 - 40) * ((100vw - 300px) / (1000 - 300)));
}

.Content span {
  display: inline-block;
  font-size: calc(14px + (20 - 14) * ((100vw - 300px) / (1000 - 300)));
  padding-top: 6px;
}

.btn-container {
  display: grid;
  grid-template-columns: 30px 30px 30px;
  grid-gap: 8px;
  margin-top: 20px;
}

.btn-container a {
  color: #0c0c0c;
}

.btn-container a:hover {
  opacity: 0.6;
}

@media only screen and (min-width: 1024px) {
  .btn-container {
    grid-template-columns: repeat(3, 40px);
    grid-gap: 16px;
  }
}
