.Design {
  margin-bottom: 94px;
}
.Design h1 {
  padding-top: 12rem;
  font-size: calc(40px + (52 - 40) * ((100vw - 300px) / (1000 - 300)));
}
.gallery {
  margin-top: 6rem;
  column-gap: 8px;
  column-count: 2;
}
.gallery img {
  width: 100%;
  padding: 3px 0;
  height: auto;
}
@media only screen and (min-width: 768px) {
  .gallery {
    column-count: 3;
  }
}
@media only screen and (min-width: 1024px) {
  .gallery {
    column-count: 4;
    column-gap: 15px;
  }
  .gallery img {
    padding: 8px 0;
  }
}
@media only screen and (min-width: 1366px) {
  .gallery {
    column-count: 5;
  }
}
