.Skills {
  margin-bottom: 94px;
}
.Skills h1 {
  padding-top: 12rem;
  font-size: calc(40px + (52 - 40) * ((100vw - 300px) / (1000 - 300)));
}
.Skills h2 {
  font-size: calc(24px + (38 - 24) * ((100vw - 300px) / (1000 - 300)));
  padding: 1rem 1rem 2rem 0px;
}
.skills-dev-container {
  margin-top: 3rem;
  margin-bottom: 4rem;
}
.skills-des-container {
  margin-top: 2rem;
}
.skills-container {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(50px, 80px));
  grid-column-gap: 40px;
  grid-row-gap: 40px;
  width: 100%;
}
.skill {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  font-size: 0.8rem;
  letter-spacing: .3px;
}
.skill svg {
  color: #0c0c0c;
  height: 80px;
  max-width: 80px;
  padding-bottom: 10px;
}
@media only screen and (min-width: 1920px) {
  .skills-container {
    grid-template-columns: repeat(auto-fill, 110px);
    grid-column-gap: 46px;
    grid-row-gap: 28px;
  }
  .skill {
    font-size: 1.1rem;
  }
  .skill svg {
    height: 110px;
    max-width: 110px;
    padding-bottom: 15px;
  }
}
