.navbar {
  position: absolute;
  height: 80px;
  width: 40px;
  z-index: 20;
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -webkit-tap-highlight-color: transparent;
}
.navbar:hover {
  cursor: pointer;
}
.menu {
  position: absolute;
  top: 60px;
  z-index: 15;
}
.fixed {
  position: fixed;
}
.line {
  height: 3px;
  width: 40px;
  top: 40px;
  right: 15px;
  background-color: #000000;
  border-radius: 100px;
}
.line.one {
  transition: 1s;
}
.line.two {
  transform: rotate(90deg);
  margin-top: -2.6px;
  transition: 1s;
}
.menu.open .line {
  background-color: #fff;
}
.menu.open .line.one {
  transform: rotate(45deg);
  transition: 1s;
}
.menu.open .line.two {
  transform: rotate(-45deg);
  transition: 1s;
}
